// Column View Bootstrap
.app-news {
	
    // General Styles
    &.sc-element>.sc-menu>li>a>div {
        background-color: #666666;
    }
    &.app-edit {
        padding-top: 40px;
	} 

	// Column View Bootstrap
    &.app-news-list {
        .col,
        > .row > .col-12  {
            margin-bottom: $grid-gutter-width;
            @include media-breakpoint-up(lg) {
                margin-bottom: $grid-gutter-width*1.5;
            }
        }

        // Categorie Info
        .app-catinfo {
            margin-bottom: $grid-gutter-width/1.5;
        }
        .app-details-link {
            position: relative;

            .link-overlay {
                position: absolute;
                display: block;
                top: 0px;
                left: 0px;
                height: 100%;
                width: 100%;
                z-index: 20;
            }

            a:not(.link-overlay) {
                position: relative;
                z-index: 30;
            }
            
			// App-Image with overlay and fallback
            .app-img {
                position: relative;
                margin-bottom: $grid-gutter-width/1.5;
                text-align: center;

                &.app-noimg {
                    background: #e9ecef;
                    height: 100%;
                    width: 100%;
                    max-height: 209px;

                    @media (max-width: 768px) {
                        height: 431px;
                        max-height: inherit;
                    }

                    img {
                        max-width: 120px;
                        margin-top: 50px;

                        @media (max-width: 768px) {
                            max-width: 220px;
                            margin-top: 130px;
                        }
    
                    }
                }
                
                span {
                    position: absolute;
                    background: rgba($primary, .8);
                    opacity: 0;
                    top: 0;
                    bottom: 0;
                    width: 100%;
                    z-index: 10;
                    transition: opacity ease-in 0.18s;
                    i {
                        display: block;
                        text-align: center;
                        color: #FFF;
                        font-size: 30px;
                        top: 50%;
                        line-height: 0;
                    }
                }
                &.app-no-img {
                    background: $gray-200 url(../res/missing_img.png) center center no-repeat;
                    background-size: 35%;
                }
			} 

			// App-Text with readmore
            .app-text {
                 h3, h2 {
                    &.app-list {
                        margin-top: $grid-gutter-width/2;
                        @include media-breakpoint-up(xs) {
                            margin-top: 0;
                        }
                    }
                }
                span {
                    font-size: $font-size-base * 0.8;
                    font-weight: bold;
                }
                .app-date {
                    &.app-date-border {
                        border-right: 1px solid $body-color;
                        padding-right: 8px;
                        margin-right: 4px;
                    }
                }
                .app-categories {
                    a {
                        letter-spacing: 1px;
                        text-transform: uppercase;
                        margin-right: 8px;
                    }
                }
                p {
                    margin-top: $grid-gutter-width/2;
                }
            }
            .app-readmore {
                color: $primary;
			}
			
			// Hover Styling
            &:hover {
                .app-img {
                    span {
                        opacity: 1;
                    }
                }
                .app-text {
                    h3, h2 {
                        color: $primary;
                    }
                }
                .app-readmore {
                    color: $body-color;
                }
            }
        }
	} 
	// END Column View Bootstrap

    // Pagination
    .app-news-paging {
        width: 100%;
        text-align: center;
        margin-top: $grid-gutter-width;
        >a {
            display: inline-block;
            height: 30px;
            width: 30px;
            text-align: center;
            border-radius: 50%;
            font-size: 16px;
            line-height: 30px;
            color: #666;
            transition: background .2s ease, color .2s ease;
            &:hover {
                background: $primary;
                color: #fff;
            }
            &.app-news-paging-active {
                background: $primary;
                color: #fff;
            }
            &.app-news-prev-pager {
                background: #fff;
            }
            &.app-news-next-pager {
                background: #fff;
            }
        }
    }

    // Styles for pills and warnings
    .app-events-pill {
        height: 10px;
        font-size: 12px;
        font-weight: normal;
        border: 1px solid #e74c3c;
        padding: 5px;
        color: #e74c3c;
        border-radius: 8px;
        font-family: Helvetica;
        vertical-align: middle;
    }

    .warning-admins { width: 100%; }
}